import React, { Component } from "react";
import "../CssComponents/CardQuote.css";

export default class CardQuote extends Component {
  state = {
    quotes: [
      "No hay que ir para atrás ni para darse impulso",
      "No hay caminos para la paz; la paz es el camino",
      "Haz el amor y no la guerra",
      " Para trabajar basta estar convencido de una cosa: que trabajar es menos aburrido que divertirse ",
      "Lo peor que hacen los malos es obligarnos a dudar de los buenos",
    ],
    authors: [
      "Lao Tsé",
      "Mahatma Gandhi",
      "John Lennon",
      "Charles Baudelaire",
      "Jacinto Benavente",
    ],
    numberRandom: -1,
  };

  componentDidMount = () => {
    this.setState({
      numberRandom: Math.floor(Math.random() * 5),
    });
  };

  changeQuote = () => {
    const previus = this.state.numberRandom;
    let next = -1;
    do {
      next = Math.floor(Math.random() * 5);
    } while (previus === next);
    this.setState({
      numberRandom: next,
    });
  };

  render() {
    return (
      <div id="quote-box">
        <h2 id="text">{this.state.quotes[this.state.numberRandom]}</h2>
        <p id="author">{this.state.authors[this.state.numberRandom]}</p>
        <div className="buttons">
          <a
            id="tweet-quote"
            href={`https://twitter.com/intent/tweet?text=${
              this.state.quotes[this.state.numberRandom]
            }`}
          >
            Tweet
          </a>
          <button id="new-quote" onClick={this.changeQuote}>
            New Quote
          </button>
        </div>
      </div>
    );
  }
}
